import {UserModel} from "../../model/user";
const  userModel = new UserModel()
export default {
  data() {
    return {
      showSignModel:false,
      list:[],
      page:1,
      size:5,
      flag:true
    };
  },
  methods: {
    getMyList()
    {
        let params = {
            page : this.page,
            size : this.size
        }
        userModel.appointmentList(params).then((res) => {
            this.list = this.list.concat(res.data);
            if(res.data.length == 0) {
                this.flag = false
            }else {
                this.flag = true
                this.page = this.page + 1;
            }
        })
    },
   toMyYuYueDetails(){
       console.log("ss")
       uni.navigateTo({
        url: "/pagesA/my_yuyue_details/my_yuyue_details"
      });
   }
  },
  components: { },
  onLoad: function() {},
  onShow: function() {
    uni.hideTabBar();
    this.getMyList();
  },
  onHide: function() {},
  onReachBottom() {
      if(this.flag) {
          this.getMyList();
      }
  }
};
